import React from 'react';

import { Col, Form, Button, Pagination } from 'react-bootstrap';

// import PropTypes from 'prop-types';
// import classNames from 'classnames'

const Sidebar = ({
	moviesWillWatch,
	moviesGenre,
	moviesGenreActive,
	currentPage,
	total_pages,
	addMoviesGenre,
	removeMoviesGenre,
	onChangePopularity,
	onChangeYear,
	paginationIncrease,
	paginationDecrease,
	// movies,
	// updateSortBy,
	// sort_by,
}) => {
	const getMoviesYearRelease = () => {
		let years = [];
		for (let index = new Date().getFullYear(); index > 1970; index--) {
			years.push(index);
		}
		return years;
	};
	const years = getMoviesYearRelease();

	const popularity = ['popularity.desc', 'revenue.desc', 'vote_average.desc'];

	return (
		<div className="card">
			<div>Will watch: {moviesWillWatch.length}</div>
			<h3>Filters</h3>
			<Form>
				<div>
					<Form.Label>Sort By:</Form.Label>
					<Form.Control as="select" onChange={onChangePopularity}>
						{popularity.map((item) => (
							<option key={item} value={item}>
								{item}
							</option>
						))}
					</Form.Control>
				</div>
				<div>
					<Form.Label>Release Year:</Form.Label>
					<Form.Control as="select" onChange={onChangeYear}>
						<option>Сhoose year:</option>
						{years.map((item) => (
							<option key={item} value={item}>
								{item}
							</option>
						))}
					</Form.Control>
				</div>
				<div>
					<Form.Label>Сhoose genre:</Form.Label>
					<div className="d-flex">
						<div>
							{moviesGenre.map(({ name, id }) => {
								const isActive = moviesGenreActive.indexOf(id) !== -1;

								return (
									<Button
										key={id}
										// outline-primary
										className={isActive ? 'active' : ''}
										variant="outline-primary"
										onClick={() => (isActive ? removeMoviesGenre(id) : addMoviesGenre(id))}>
										{name}
									</Button>
								);
							})}
						</div>
					</div>
				</div>

				<Button variant="primary">Clear filter</Button>

				<div className="pagination">
					<div className="btn-group d-flex align-items-center">
						<Button variant="primary" onClick={() => paginationDecrease()}>
							Back
						</Button>

						<div>{`${currentPage} из ${total_pages}`}</div>

						<Button
							variant="primary"
							onClick={() => {
								paginationIncrease();
							}}>
							Forward
						</Button>
					</div>
				</div>
			</Form>
		</div>
	);
};

export default Sidebar;
